import { UPDATE_INDEX, ADD_INDEX, UPDATE_MODAL } from "../actions/types";

const form_state = (
  state = {
    index: 0,
    direction: null,
    modal: false
  },
  action
) => {
  switch (action.type) {
    case UPDATE_INDEX:
      return { ...state, index: action.payload };
    case ADD_INDEX:
      return { ...state, index: action.payload };
    case UPDATE_MODAL:
      return { ...state, modal: action.payload.showModal, dish: action.payload.dish };
    default:
      return state;
  }
};

export default form_state;
