import React from "react";
import { connect } from "react-redux";

import { MenuStyleComponent } from "./MenuStyle";

import foto1 from "../../../../images/menu.jpg";
import MainDish from "./MainDish";
import DishOptions from "./DishOptions";
import ModalDish from "./ModalDish";

const Menu = props => {
  return (
    <MenuStyleComponent>
      <img src={foto1} alt="familia" />
      <div className="attendance">
        <p>{props.lenguage.Menu.mainText}</p>
        <MainDish />
        <p style={{ fontWeight: "bold" }}>{props.lenguage.Menu.question}</p>
        <DishOptions {...props} />
        <ModalDish/>
      </div>
    </MenuStyleComponent>
  );
};

const mapStateToProps = ({ authReducer }) => {
  const { user, lenguage } = authReducer;

  return {
    user,
    lenguage
  };
};

export default connect(
  mapStateToProps,
  {}
)(Menu);
