import {
  USERNAME_CHANGED,
  PASSWORD_CHANGED,
  LOGIN_USER,
  LOGIN_USER_SUCCESS,
  LOGIN_USER_FAIL,
  UPDATE_PARTICIPATION,
  UPDATE_BUS,
  UPDATE_MENU
} from "../actions/types";

import spanish from "../../textContent/spanish.json";
// import english from '../../textContent/english.json'

const user_state = (
  state = {
    username: "",
    password: "",
    isWaiting: false,
    authenticated: false,
    email: "",
    user: undefined,
    participants: undefined,
    lenguage: spanish,
    error: ""
  },
  action
) => {
  switch (action.type) {
    case USERNAME_CHANGED:
      return { ...state, username: action.payload };
    case PASSWORD_CHANGED:
      return { ...state, password: action.payload };
    case LOGIN_USER:
      return { ...state, isWaiting: true, error: "" };
    case LOGIN_USER_SUCCESS:
      return {
        ...state,
        user: action.payload,
        authenticated: true,
        isWaiting: false
      };
    case LOGIN_USER_FAIL:
      return { ...state, error: action.payload, isWaiting: false };
    case UPDATE_PARTICIPATION:
      return { ...state, user: action.payload };
    case UPDATE_BUS:
      return { ...state, user: action.payload };
    case UPDATE_MENU:
      return { ...state, user: action.payload };
    default:
      return state;
  }
};

export default user_state;
