import React, { Component } from "react";
import { connect } from "react-redux";
import {
  updateParticipation,
  updateIndexFormPage,
  addIndex
} from "../../actions/weddingFormActions";

import Asistencia from "./Asistencia/Asistencia";
import Bus from "./Bus/Bus";
import Menu from "./Menu/Menu";
import Resumen from "./Resumen/Resumen";

import { WeddingFormStyleComponent } from "./WeddingFormStyle";

import Fade from "@material-ui/core/Fade";
import CircularProgress from "@material-ui/core/CircularProgress";

import Icon from "@material-ui/core/Icon";
import Button from "@material-ui/core/Button";

import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from "react-responsive-carousel";

class WeddingForm extends Component {
  constructor(props) {
    super();
    this.props = props;
    this.state = {
      index: 0,
      loading: true,
      display: false,
      carousel: false
    };
  }

  componentDidMount() {
    this.displayLoading();
  }

  displayLoading = () => {
    setTimeout(() => {
      this.setState({ ...this.state, display: true }, () =>
        this.displayCarousel()
      );
    }, 800);
  };

  displayCarousel = () => {
    setTimeout(() => {
      this.setState({
        ...this.state,
        carousel: true,
        display: false,
        loading: false
      });
    }, 1300);
  };

  // handleSelect = (index, props) => {
  //   props.updateIndexFormPage(index);
  // };

  nextSection = () => {
    this.setState({ ...this.state, index: this.state.index + 1 });
  };

  backSection = () => {
    this.setState({ ...this.state, index: this.state.index - 1 }, () => {
      window.scrollTo(0, 0);
    });
  };

  render() {
    const { loading, display, carousel } = this.state;
    return (
      <WeddingFormStyleComponent>
        <div
          className="loading"
          style={display ? { display: "flex" } : { display: "none" }}
        >
          <p>Viva los novios!</p>
          <Fade
            in={loading}
            style={{
              transitionDelay: loading ? "800ms" : "0ms"
            }}
            unmountOnExit
          >
            <CircularProgress />
          </Fade>
        </div>
        <div style={carousel ? { display: "block" } : { display: "none" }}>
          <Carousel
            showThumbs={false}
            showArrows={false}
            swipeable={false}
            showStatus={false}
            showIndicators={false}
            selectedItem={this.state.index}
          >
            <div className="section">
              <Asistencia />
              <div className="buttonSection2">
                <Button
                  variant="contained"
                  color="primary"
                  onClick={this.nextSection}
                  style={{ backgroundColor: "#3b9b53" }}
                >
                  Bus
                  <Icon>directions_bus</Icon>
                </Button>
              </div>
            </div>
            <div className="section">
              <Bus />
              <div className="buttonSection rowButtons">
                <Button
                  variant="contained"
                  color="primary"
                  onClick={this.backSection}
                  style={{ backgroundColor: "#3b9b53", marginRight: "2%" }}
                >
                  Asistencia
                  <Icon>accessibility</Icon>
                </Button>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={() => {
                    document.getElementsByTagName("body")[0].style.overflow =
                      "scroll";
                    this.nextSection();
                  }}
                  style={{ backgroundColor: "#3b9b53" }}
                >
                  Menu
                  <Icon>restaurant</Icon>
                </Button>
              </div>
            </div>
            <div className="section">
              <Menu />
              <div className="buttonSection rowButtons" >
                <Button
                  variant="contained"
                  color="primary"
                  onClick={() => {
                    document.getElementsByTagName("body")[0].style.overflow =
                      "hidden";
                    this.backSection();
                  }}
                  style={{ backgroundColor: "#3b9b53", marginRight: "2%"  }}
                >
                  Bus
                  <Icon>directions_bus</Icon>
                </Button>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={() => {
                    window.scrollTo(0,0)
                    document.getElementsByTagName("body")[0].style.overflow =
                      "hidden";
                    this.nextSection();
                  }}
                  style={{ backgroundColor: "#3b9b53" }}
                >
                  Resumen
                  <Icon>list_alt</Icon>
                </Button>
              </div>
            </div>
            <div className="section">
              <Resumen />
              <div className="buttonSection rowButtons" >
                <Button
                  variant="contained"
                  color="primary"
                  onClick={() => {
                    document.getElementsByTagName("body")[0].style.overflow =
                      "scroll";
                    this.backSection();
                  }}
                  style={{ backgroundColor: "#3b9b53", marginRight: "2%"  }}
                >
                  Menu
                  <Icon>restaurant</Icon>
                </Button>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={() => {
                    document.getElementsByTagName("body")[0].style.overflow =
                      "hidden";
                    this.backSection();
                  }}
                  style={{ backgroundColor: "#3b9b53" }}
                >
                  Enviar
                  <Icon>send</Icon>
                </Button>
              </div>
            </div>
          </Carousel>
        </div>
      </WeddingFormStyleComponent>
    );
  }
}

const mapStateToProps = ({ authReducer, weddingFormReducer }) => {
  const { user } = authReducer;
  const { index, direction } = weddingFormReducer;

  return {
    user,
    index,
    direction
  };
};

export default connect(
  mapStateToProps,
  { updateParticipation, updateIndexFormPage, addIndex }
)(WeddingForm);
