import React from "react";
import { updateModal } from "../../../actions/weddingFormActions";
import { connect } from "react-redux";

import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";

const handleClose = props => {
  props.updateModal(false);
};

const ModalDish = props => {
  console.log("Menu", props);
  return (
    <Dialog
      open={props.modal}
      onClose={() => handleClose(props)}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">
        {props.dish !== undefined && props.dish.title}
      </DialogTitle>
      <DialogContent>
        <DialogContentText id="alert-dialog-description">
          {props.dish !== undefined && props.dish.description}
          <img 
        style={{}}
        src= {props.dish !== undefined && props.dish.img} alt={props.dish !== undefined && props.dish.title} />
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={() => handleClose(props)} color="primary">
          Disagree
        </Button>
        <Button onClick={() => handleClose(props)} color="primary" autoFocus>
          Agree
        </Button>
      </DialogActions>
    </Dialog>
  );
};

const mapStateToProps = ({ weddingFormReducer }) => {
  const { modal, dish } = weddingFormReducer;

  return {
    modal,
    dish
  };
};

export default connect(
  mapStateToProps,
  { updateModal }
)(ModalDish);
